	import service from "../../service.js";
	import headers from "../../components/header_nav.vue"
	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
	export default {
		components: {
			uniLoadMore,
			headers
		},
		data() {
			return {
				Htitle: "选择兑换券",
				Hlicon: "../../static/icon/left_whitch.png",
				funlist:[],
				money:0,
				merchid:0,
				type:0,
			}
		},
		onShow:function(){
			var item = uni.getStorageSync('item');
			this.money = item[1];
			this.merchid = item[0];
			service.auth(this , service.api.main.merch_list , 'POST' , {
				id:this.merchid
			} , function(self,res){
				res = res.data.data[0];
				var type = res.type;
				self.type = res.type;
				
				service.auth(self , service.api.service.exchange_log , 'POST' , {
					status:1
				} , function(self,res){
					var list = res.data.data;
					//self.funlist = list;
					for(var i = 0;i < list.length;i++){
						if(list[i].coupon.catid == type){
							self.funlist.push(list[i]);
						}
					}
					if(self.funlist.length == 0){
						uni.showModal({
							title: '温馨提示',
							content: '您的账户暂无兑换券',
							confirmText: '确认',
							confirmColor: '#FFAE00',
							cancelColor: '#D2D2D2',
							showCancel: false,
							success: function(res) {
								uni.switchTab({
									url: '/pages/main/index',
								});
							}
						});
					}
				});
			});
			
		},
        methods: {
			submit(id,money){
				var self = this;
				uni.showModal({
					content: '确认使用此兑换券吗？',
					cancelText: '取消',
					confirmText: '确认',
					confirmColor: '#FFAE00',
					cancelColor: '#D2D2D2',
					success: function(res) {
						if (res.confirm) {
							service.downLoad(self, service.api.main.exchange, {
								'merchid': self.merchid,
								'money':self.money,
								'c_money':money,
								'cid':id,
							},'POST', function(self, res) {
								console.log(res)
								uni.showToast({
									icon: 'none',
									title: res.info
								});
								if(res.code == 1){
									setTimeout(function(){
										uni.navigateBack({
											delta:1
										});
									},1500);
									
								}
							});
						}
					}
				});
				
			}
		}
	}
